@import "reset";
@import "mixins";
@import "vars";
:root {
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
  }
}

html,
body {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  font-family: Arial, Helvetica, sans-serif;
  @include rfs(15);
}

.frontpage {
  height: 100%;
  min-height: 100vh;
  background-image: url("../img/bg__night-city.jpg");
  background-size: cover;
}

.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  max-width: 1160px;
  margin: 0 auto;
  @media only screen and (max-width: 1200px) {
    padding-left: 1%;
    padding-right: 1%;
  }
}

.header {
  padding-top: 41px;
  display: flex;
  @media only screen and (max-width: 768px) {
    justify-content: center;
  }
  .header__logo {
    max-width: 135px;
    img {
      width: 100%;
    }
  }
}

.content {
  flex: 1 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  .input-block__title {
    margin-bottom: 37px;
    @include rfs(38px);
    text-align: center;
    color: #fff;
  }
  .input-block {
    width: 100%;
    padding-bottom: 12px;
  }
  .input-block__form {
    margin: 0 auto;
    width: 100%;
    max-width: 825px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    @media only screen and (max-width: 825px) {
      width: 100%;
    }
  }
  .form__notify,
  input {
    height: 2.8em;
    @include rfs(20px);
    padding-left: 12px;
    border: 1px solid transparent;
  }
  .form__address {
    flex-grow: 1;
    flex-shrink: 10;
    margin-right: -3px;
    padding-right: 10px;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
  }
  .form__unit {
    max-width: 230px;
  }
  .form__notify {
    margin-left: -2px;
    padding-left: 20px;
    padding-right: 20px;
    font-weight: 500;
    text-align: center;
    color: #fff;
    background-color: #8cc63f;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
    box-sizing: border-box;
    &:hover {
      cursor: pointer;
      background-color: darken(#8cc63f, 5%);
    }
  }
  .form__notify:focus,
  input:focus {
    outline: none;
  }
  input::-webkit-input-placeholder {
    color: #2e2e2e;
  }
  input:-ms-input-placeholder {
    color: #2e2e2e!important;
  }
  input::placeholder {
    color: #2e2e2e;
  }
  @media only screen and (max-width:600px) {
    .form__notify,
    input {
      width: 100%;
      margin-bottom: 15px;
      border-radius: 3px
    }
    .form__notify {
      margin-top: 30px;
      max-width: 50%;
    }
    .form__unit {
      max-width: 100%;
    }
  }
}

.footer {
  flex: 0 0 auto;
}

.footer__menu {
  display: flex;
  padding-bottom: 46px;
  justify-content: space-between;
  @media only screen and (max-width: 480px) {
    padding-bottom: 23px;
  }
}

.footer__menu-item {
  margin-right: 25px;
  @include rfs(17px);
  color: #fff;
  white-space: nowrap;
  transition: all .2s ease;
  &:hover {
    color: #8cc63f;
  }
  &:last-child {
    margin-right: 0;
  }
}

@import "media";